@import url(normalize.css);
@import url(http://fonts.googleapis.com/css?family=Playfair+Display:400italic,700italic,);
body {
  font-size: 100%;
  line-height: 1.25; }

html.home {
  height: 100%; }
  html.home body {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    margin: 0; }
    html.home body #menu, html.home body main {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      will-change: transform;
      transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946); }
    html.home body #menu {
      width: 300px;
      transform: translateX(-300px); }
    html.home body.view-change #menu {
      transform: translateX(0); }
    html.home body.view-change main {
      transform: translateX(300px); }
  html.home body {
    background: #fff;
    color: #555; }
    html.home body #menu {
      background: #555;
      color: #fff; }
    html.home body main #header {
      position: fixed;
      left: 1.25em;
      top: 1.25em;
      cursor: pointer; }
      html.home body main #header h1 {
        font-family: Playfair Display, serif;
        font-weight: 400;
        font-style: italic;
        font-size: 1em; }
        html.home body main #header h1 a {
          text-decoration: none;
          background: #fff;
          color: #555; }
    html.home body main #logo {
      font-family: Europa, sans-serif;
      font-weight: 300;
      font-style: normal;
      font-size: 1em;
      text-transform: uppercase;
      letter-spacing: 1.25px;
      will-change: transform;
      transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
      display: flex;
      align-items: center;
      position: fixed;
      bottom: 1.25em;
      left: 1.25em;
      cursor: pointer; }
      html.home body main #logo span {
        display: inline-block; }
        html.home body main #logo span.first-letter span {
          width: 2.5em;
          height: 2.5em;
          border: 1px solid;
          border-radius: 2.5em;
          display: flex;
          justify-content: center;
          align-items: center;
          transform: rotate(-90deg); }
        html.home body main #logo span.site-title {
          margin-left: 1.25em;
          display: none; }
    html.home body.view-change main #logo {
      background: #555;
      color: #fff;
      transform: translateX(-300px); }
      html.home body.view-change main #logo span.first-letter span {
        transform: rotate(0deg); }
      html.home body.view-change main #logo span.site-title {
        display: inline-block; }

